import { useState } from "react";
export const Primercomponente=()=>{
    /* codigo js */
    /* hooks renderiza en tiempo real una variable */
    /* hook count variable, setCount porpiedad para actualizar la variable */
    /* camelCase */
    const [count , setCount]=useState(0);
    /* setcount cambia el valor ingresado */

     function sumar(){
       
        
        setCount(count+1)
     }
     function resta({target}){
        if(count>0){
            setCount(count-1)
            target.disabled=false;
        }
        target.disabled=true;
     }
    return(
        /* codigo html */
        /* debe estar dentro de un padre */
        /* mejor usar fragmentos <> para escribir html */
        <><div>
               <h1 className="text-primary">Primer componente</h1>

              <p>contador: {count}</p>
              <button onClick={sumar} className="btn btn-primary ">+</button>
              <button onClick={resta} className="btn btn-danger "  >-</button>
        </div>
       
        </>


        
    )
};
